<template>
  <div class="main_inex_change_info">
    <mt-field class="mt_item" disabled label="微信头像">
      <div class="tx_div_wapper common_back hasBg" id="change_nick" @click="changeHeadImg"></div>
    </mt-field>
    <!-- 昵称 -->
    <mt-field label="微信昵称" placeholder="必填" v-model="nickForm.nickName"></mt-field>
    <!-- 封面 -->
    <mt-field class="mt_item" disabled label="背景封面">
      <div class="tx_div_wapper common_back hasBg" id="back_bk" @click="changeBkImg"></div>
    </mt-field>
    <!-- 未读消息 -->
    <mt-field class="mt_item" disabled label="未读消息">
      <mt-switch v-model="nickForm.ifNoRead"></mt-switch>
    </mt-field>
    <span v-show="nickForm.ifNoRead">
      <!-- 未读头像 -->
      <mt-field class="" disabled label="未读消息头像">
        <div class="tx_div_wapper common_back hasBg" id="no_read_img" @click="changeReadImg"></div>
      </mt-field>
      <!-- 未读消息数量 -->
      <mt-field class="" type="number" label="未读消息数" placeholder="填写数字" v-model="nickForm.noReadNum">
      </mt-field>
    </span>
    <!-- 朋友动态 -->
    <mt-field class="mt_item" disabled label="朋友新动态">
      <mt-switch v-model="nickForm.ifNewDt"></mt-switch>
    </mt-field>
    <mt-field v-show="nickForm.ifNewDt" class="" label="朋友头像">
      <div class="tx_div_wapper common_back hasBg" id="fir_dt_img" @click="changeFriImg"></div>
    </mt-field>
    <!-- 上传组件 -->
    <common-upload ref="loadImg" :fatherClass="'changenick'" :ifM="setUploadM" @setImg="setImg" :needWidHei="true"></common-upload>
    <!-- 评论头像选择 -->
    <mt-popup v-model="popupVisible14" class="app_pop_wapper">
      <div class="app_pop_inner">
        <div class="pop_item" @click="addImg('local')">相册上传</div>
        <div class="pop_item" @click="addImg('sj1')">随机1条</div>
      </div>
    </mt-popup>
    <!-- 生成按钮 -->
    <div class="inner_make_btn mt_item">
      <mt-button type="primary" size="small" @click="postForm">保存</mt-button>
      <!-- <mt-button size="small" type="danger" @click="delForm">清空</mt-button> -->
      <mt-button type="primary" plain size="small" @click="backBtn">返回</mt-button>
    </div>
    <div v-if="!canPost" class="tips_text">头像、昵称、背景图填写完整</div>
  </div>
</template>
<script>
import { Toast } from 'mint-ui';
import { returnImg } from "@/utils/makeJson.js"
import commonUpload from '@/components/commonUpload2'

export default {
  components: {
    commonUpload
  },
  data() {
    return {
      popupVisible14: false,
      setUploadM: false,
      doubleChoseType: 'headImg',
      canPost: true,
      nickForm: {
        yourHeaderImg: '', //头像
        nickName: '', //昵称
        bkImg: '', //背景图
        ifNoRead: false, //是否显示未读
        noReadImg: '', //未读头像
        noReadNum: 1, //未读数
        ifNewDt: false, //是否显示朋友的新动态
        firNewImg: '', //朋友动态的头像
      }
    }
  },
  mounted() {

  },
  methods: {
    checkForm() {
      if (!this.nickForm.yourHeaderImg || !this.nickForm.nickName || !this.nickForm.bkImg) {
        // 提示填写完整
        this.canPost = false
      } else {
        this.canPost = true
      }
      return this.canPost
    },
    postForm() {
      if (!this.checkForm()) return
      // 保存
      this.$emit('nickInfo', this.nickForm)
      // 关闭本弹窗
      this.backBtn()
    },
    delForm() {},
    backBtn() {
      this.$emit('close')
    },
    changeHeadImg() {
      this.doubleChoseType = 'headImg'
      // 选择头像
      this.popupVisible14 = true
    },
    changeBkImg() {
      // 选择背景图
      this.controlChange('bkImg', false)
    },
    changeReadImg() {
      this.doubleChoseType = 'noReadImg'
      // 选择未读头像
      this.popupVisible14 = true
    },
    changeFriImg() {
      // 选择动态的头像
      this.doubleChoseType = 'friDt'
      // 选择未读头像
      this.popupVisible14 = true
    },
    setImg(urls, wid, hei) {
      // 从组件那里获取到头像
      console.log(this.whiosGetImg)
      switch (this.whiosGetImg) {
        case 'headImg':
          this.nickForm.yourHeaderImg = urls[0]
          // 头像图片显示
          $("#change_nick").css({
            'background-image': 'url(' + urls[0] + ')'
          })
          break;
        case 'bkImg':
          // 背景图片显示
          this.nickForm.bkImg = urls[0]
          $("#back_bk").css({
            'background-image': 'url(' + urls[0] + ')'
          })
          // 根据图片宽高决定样式
          this.setBkImgWid(wid, hei)
          break;
        case 'noReadImg':
          // 未读头像
          this.nickForm.noReadImg = urls[0]
          $("#no_read_img").css({
            'background-image': 'url(' + urls[0] + ')'
          })
          break;
        case 'friDt':
          // 未读头像
          this.nickForm.firNewImg = urls[0]
          $("#fir_dt_img").css({
            'background-image': 'url(' + urls[0] + ')'
          })
          break;


      }
    },
    setBkImgWid(wid, hei) {
      // 根据图片宽高决定样式
      console.log($('.top_fake_bk'))
      if (wid && hei) {
        if (wid > hei) {
          console.log(' 宽>高,背景图片高度100%')
          // 宽>高,背景图片高度100%
          $('.top_fake_bk').css({
            'height': '100%',
            'width': 'auto'
          })
        } else if (hei > wid) {
          // 宽<高,宽度100%
          console.log('宽<高,宽度100%')
          $('.top_fake_bk').css({
            'width': '100%',
            'height': 'auto'
          })
        } else {
          // 默认样式
          $('.top_fake_bk').css({
            'width': '100%',
            'height': 'auto'
          })
        }
      }
    },
    controlChange(whios, ifM) {
      this.whiosGetImg = whios
      // 上传设置单选
      this.setUploadM = ifM
      // 修改头像,触发组件
      setTimeout(() => {
        this.$refs.loadImg.actions()
      }, 100)
    },
    addImg(type) {
      // 增加点赞
      switch (type) {
        case 'local':
          // 本地上传
          this.controlChange(this.doubleChoseType, false)
          break;
        case 'sj1':
          // 随机1张
          this.whiosGetImg = this.doubleChoseType //告诉应该把图片给谁
          this.setImg(returnImg(1, 0, 300))
          break;
      }
      this.popupVisible14 = false
    }
  },
  watch: {}
}

</script>
<style scoped lang="scss">
.main_inex_change_info {
  background-color: #eae9eb;
  width: 100%;
  height: 100%;
  // padding: 20px;
  overflow: auto;
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;

  .tips_text {
    color: red;
    text-align: center;
    font-size: 24px;
  }

  .inner_make_btn {
    // margin-top: 30px;
    padding: 30px 0;
    text-align: center;
    background: #fff;
  }

  .mt_item {
    margin-top: 30px;
  }

  .tx_div_wapper {
    width: 128px;
    height: 128px;

    &.hasBg {
      background-image: url('../../../assets/nodoby.jpg')
    }
  }
}

</style>



